
.account-nav{font-size:1.08333em;display:flex;list-style:none;margin:10px 0;padding:0;justify-content:stretch
}
.account-nav li{flex:1
}
.account-nav li a{display:block;text-align:center;text-decoration:none;color:inherit;text-transform:uppercase
}
.account-nav li a span{border-bottom:1px solid transparent;padding-bottom:2px
}
.account-nav li a.router-link-exact-active span{border-color:#FF36AD
}
.account-header{padding:12px 15px;display:flex;align-items:center
}
.account-header .account-item{flex:1
}
.menu-link{flex:28px 0 0;width:28px;height:28px;margin:0 8px 0 -6px;background:url(data:image/svg+xml;base64,PHN2ZyB4bWxucz0iaHR0cDovL3d3dy53My5vcmcvMjAwMC9zdmciIHdpZHRoPSIyNCIgaGVpZ2h0PSIyNCI+PHBhdGggZmlsbD0ibm9uZSIgc3Ryb2tlPSIjRjczQUE5IiBkPSJNMTUgNmwtNiA2IDYgNiIvPjwvc3ZnPg==);background-size:24px;background-repeat:no-repeat;background-position:center center;border-radius:100%
}
.menu-link:hover{background-color:#f4f4f4
}
.account-name{font-weight:500
}
.account-item{line-height:15px;display:flex
}
.account-item .account-item-text{flex:1;display:flex;flex-direction:column
}
.account-item .account-item-text .account-address-chain{white-space:nowrap;display:flex;max-width:230px
}
.account-item .account-item-text .account-address-chain .address{flex:1;text-overflow:ellipsis;overflow:hidden
}
.account-item .identicon{padding:2px;border:1px solid #eee;margin-right:7px
}
.account-item .identicon svg{display:block;width:26px;height:26px
}
.external-link{text-decoration:none;padding-right:20px;background:url(data:image/svg+xml;base64,);background-size:16px;background-repeat:no-repeat;background-position:100% 0
}
